import Vue from 'vue'
import VueRouter from 'vue-router'
import Qrcode from '../views/Qrcode.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Qrcode',
    component: Qrcode
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
