<template>
    <div class="wrap">
         <div class="cover" >
              <div class="img_wrap">
                <div class="title">{{rdrno || '二维码电子证'}}</div>
                   <div class="qrcode" ref="qrcode" /> 

              </div>
            </div>
    </div>
</template>
<script>
//导入js

import QRCode from 'qrcodejs2'
import{getUrlParam} from '../utils/index'
export default {
    data(){
        return{
            rdrno:' '
        }
    },
    created(){
            let rdrno = getUrlParam('rdrno')
            if(!rdrno){
                rdrno = '未获取正确的读者证号'
            }
            this.rdrno = rdrno
    },
  mounted (){
      this.$nextTick(()=>{

             var canvas = this.$refs.qrcode;

             new QRCode(canvas, {

                 text:this.rdrno, 

                 width:205,

                 height:205,

                 colorDark : "#000",   //二维码颜色

                 colorLight : "#ffffff"   //二维码背景色

       })

   })
 }
}
</script>

<style lang="scss" scoped>
      .cover {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, .7);
      z-index: 100;
      display: flex;
      justify-content: center;
      align-items: center;

      .img_wrap {
        background: #fff;
        border-radius: 10px;
        height: 385px;
        width: 295px;


        display: inline-flex;
        flex-direction: column;
